	module.exports = {"data":{"wordpressPost":{"title":"Rihanna&#8217;s Fenty Beauty Is Here At Last And It Is PERFECTION","content":"<p><strong>FENTY Beauty</strong> is here and guess what &#8211; it is EVERYTHING you wished for.</p>\n<p>We were lucky enough to get a sneak peak before the range was released to the public and heck &#8230; we&#8217;re kind of speechless and frankly &#8211; that does not happen often.</p>\n<p><strong>The FENTY range</strong>, which you&#8217;ll already know is the brainchild of our favourite rock rebel Rihanna, is available at <a href=\"https://www.sephora.com.au/brands/fenty-beauty?acidi=2829&amp;aaidi=15110&amp;acn=[AU,+NZ]+Fenty+#1:+Launch&amp;ach=Main+Billboard\">Sephora </a>which is where we had the opportunity to get up close and personal with it early today.</p>\n<p>There was a <strong>LOT of secrecy</strong> around this range. For real &#8230; For example, we had to sign a non-disclosure agreement before we were even allowed to see it. Sign it twice in fact.</p>\n<figure id=\"attachment_3113\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3113\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/08211837/riri-highlighter-JPG-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/08211837/riri-highlighter-JPG-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/08211837/riri-highlighter-JPG-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/08211837/riri-highlighter-JPG.jpg 596w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">The texture, the tone. Quite simply &#8211; out of this world. Image: Instagram/@badgalriri</figcaption></figure>\n<p>What&#8217;s a non-disclosure agreement? Well, it&#8217;s a legally-binding document in which you swear to secrecy but guess what &#8211; we&#8217;re going to spill the beans anyway &#8211; well some of them. Because, like you, we have access to the internet and there is a LOT of information out there about this range and while we can&#8217;t tell you everything &#8211; we can certainly report on what other people are saying &#8211; and so far, it is ALL positive!</p>\n<p>We can see why too. This range might be celebrity-endorsed but it has much more going for it than just that. Indeed, it really is quite something.</p>\n<figure id=\"attachment_3114\" style=\"width: 241px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3114\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/08211949/riri-JPG-241x300.jpg\" alt=\"\" width=\"241\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/08211949/riri-JPG-241x300.jpg 241w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/08211949/riri-JPG.jpg 479w\" sizes=\"(max-width: 241px) 100vw, 241px\" /><figcaption class=\"wp-caption-text\">Riri &#8211; on the cover of Elle US &#8211; looking KILLER. Image: Instagram/@badgalriri</figcaption></figure>\n<p>For starters, the packaging is cool as heck and much of it in a super-modern hexagonal shape.</p>\n<p>The textures are creamy and lush and the colours flattering and seriously pretty. There&#8217;s creamy, natural foundations and shimmery, flattering highlighters. There are primers that actually work (yep &#8211; for real), skin sticks, lipsticks and tools too. We&#8217;re talking soft, fluffy brushes, blotting powders and much, much more.</p>\n<p>Will you want some? Damn straight you will. The only question will be just how much. A little? Maybe? A lot? More than likely. Hop on over to Sephora and get yours now. And if we elbow you out of the way don&#8217;t take it personally okay? We just really want this stuff &#8211; bad.</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/08205658/Fenty-JPG.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"a1ed1494-7af2-5f8f-812c-60c0e995a0bc"}}

